import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { getTime }                                  from "Helpers";
import { useHandlerMutation, useValidDateToSubmit } from "Helpers/Hooks";
import { AppraisalHOCPageFetcher }                  from "Components/Appraisal";
import Conclusion                                   from "./Conclusion";
import { PARAMS, DEFAULT_FETCH_DATA}                from "./ConclusionConstants";


const ConclusionContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { conclusions }  = appraise;
	// ------------> hookForm <---------------
	const defaultValues = {
		...conclusions,
		referred_val_date : getTime(conclusions?.referred_val_date),
	};

	const hookForm = useForm({
		defaultValues,
		shouldUnregister : true,
	});

	const { control, handleSubmit } = hookForm;

	const getValidDateToSubmit = useValidDateToSubmit({ control });

	const onSubmit =  handleSubmit(async (formData) => {

		const { referred_val_date } = formData;

		const dataToSubmit = {
			...formData,
			referred_val_date : getValidDateToSubmit(referred_val_date, conclusions?.referred_val_date, "referred_val_date"),
		};
		await editApprasial({
			module : `/appraisals/conclusions/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<Conclusion
			delegations={{
				onSubmit,
				hookForm,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(ConclusionContainer, PARAMS);
