import { HeaderPage, Tabs, ValueResume, ConclusionSection } from "Components/Appraisal";

const tabs = [
	{
		title : "Resumen de Valores",
		Tab   : ValueResume,
	},
	{
		title : "ConclusiÃ³n",
		Tab   : ConclusionSection,
	},
];

const Conclusion = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={[2, 0]}
				section="footer"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>

			<Tabs
				tabs={tabs}
				hookForm={hookForm}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
		</>
	);
};

export default Conclusion;
